<template>
	<div> </div>
</template>

<script>
	export default {
		name: "Votes",
		props: ['itemId'],
		methods: {
			async postEvent(data) {
				try {
					await this.$http.post(`${this.$api}/events/`, data)
					this.removed = true
				} catch (e) {
					console.error(e)
				}
			},
		},
		created() {
			this.postEvent({
				itemId: this.itemId,
				category: 'Comment',
				event: 'loaded'
			})
		}
	}
</script>

